import React, { Component } from 'react';
import './view2.css';
import { Collapse, Checkbox, Slider, Select } from 'antd';


const { Panel } = Collapse;
const CheckboxGroup = Checkbox.Group;
const importanceOptions = ['Top-Class', 'High-Class', 'Mid-Class', 'Unknown-Class', 'NA-Class'];
const qualityOptions = ['FA-Class', 'GA-Class', 'A-Class', 'B-Class', 'C-Class', 'Start-Class', 'Stub-Class', 'FL-Class', 'List-Class', 'Unassessed-Class', 'NA-Class'];

export default class View2 extends Component {

    constructor(props) {
        super(props);
        this.state = {
            checkedImportance: importanceOptions,
            checkedQuality: qualityOptions,
            indeterminate: true,
        };
    }

    onChangeImportance = checkedImportance => {
        this.setState({
            checkedImportance,
            indeterminate: !!checkedImportance.length && checkedImportance.length < importanceOptions.length,
            checkAll: checkedImportance.length === importanceOptions.length,
        });
        this.props.changeIncludedImportances(checkedImportance);
    };

    onChangeQuality = checkedQuality => {
        this.setState({
            checkedQuality,
            indeterminate: !!checkedQuality.length && checkedQuality.length < qualityOptions.length,
            checkAll: checkedQuality.length === qualityOptions.length,
        });
        this.props.changeIncludedQualities(checkedQuality);
    };

    onChangeImagesRange = value => {
        this.props.changeImagesRange(value);
    }

    onChangeCategories = value => {
        this.props.changeCategories(value);
    }

    render() {
        return (
            <div id='view2' className='pane'>
                <Collapse ghost={true} defaultActiveKey={['0', '1', '2', '3']}>
                <Panel header="Quality">
                <div style={{ width: 275, margin: 5 }}>
                    <CheckboxGroup
                        options={qualityOptions}
                        value={this.state.checkedQuality}
                        onChange={this.onChangeQuality}
                    />
                </div>
                </Panel>
                <Panel header="Importance">
                <div style={{ width: 275, margin: 5 }}>
                    <CheckboxGroup
                        options={importanceOptions}
                        value={this.state.checkedImportance}
                        onChange={this.onChangeImportance}
                    />
                </div>
                </Panel>
                <Panel header="Number of images">
                    <Slider
                        range={{ draggableTrack: true }}
                        defaultValue={[0, this.props.maxNumberOfImages]}
                        max={this.props.maxNumberOfImages}
                        onChange={this.onChangeImagesRange}
                    />
                </Panel>
                <Panel header="Categories">
                    <Select
                        mode="multiple"
                        style={{ width: '100%' }}
                        placeholder="All categories"
                        options={this.props.allCategories}
                        onChange={this.onChangeCategories}
                        
                    />
                </Panel>
                </Collapse>
                
            </div>
        )
    }
}
