import React, { Component } from 'react';
import { Table } from 'antd'
import draw from './vis';

const dataSource = [
    {
      key: '1',
      name: 'Mike',
      age: 32,
      address: '10 Downing Street',
    },
    {
      key: '2',
      name: 'John',
      age: 42,
      address: '10 Downing Street',
    },
  ];
  
  const columns = [
    {
      title: 'Artilcle',
      dataIndex: 'article',
      key: 'article',
    },
    {
      title: 'Quality',
      dataIndex: 'quality',
      key: 'quality',
    },
    {
      title: 'Importance',
      dataIndex: 'importance',
      key: 'importance',
    },
    {
        title: 'Number of images',
        dataIndex: 'n_images',
        key: 'n_images',
      },
    {
        title: 'Categories',
        dataIndex: 'categories',
        key: 'categories',
      },
  ];

export default class TestChart extends Component {

    componentDidMount() {
        draw(this.props);
    }

    componentDidUpdate(preProps) {
        draw(this.props);
    }

    render() {
        return (
            <div>
            <Table 
                dataSource={this.props.data}
                columns={columns}
                pagination={{ position: ['bottomCenter'] }}
            />
            <div className='test-chart'/>
            </div>
        )
    }
}