import React, { Component } from 'react';
import './App.css';
import data from './data/wp_Tirana.json';
import {Layout} from "antd"
import View1 from './views/view1';
import View2 from './views/view2';

const { Sider, Content } = Layout;

function findAllCategories () {
  var categories = [...new Set(data.articles.map(d => d.categories).join(",").split(','))]
  var categoriesOptions = []
  categories.forEach(function(c){
      var cat = {value: c, label: c}
      categoriesOptions.push(cat)
  });

  return categoriesOptions
}

const allCategories = findAllCategories()

export default class Dashboard extends Component {

  maxNumberOfImages = data.articles.sort( 
    function(a, b) {
       return parseFloat(b['n_images']) - parseFloat(a['n_images']);
    }
  )[0]['n_images']

  constructor(props) {
      super(props);
      this.state = {
        includedImportances: ['Top-Class', 'High-Class', 'Mid-Class', 'Unknown-Class', 'NA-Class'],
        includedQualities: ['FA-Class', 'GA-Class', 'A-Class', 'B-Class', 'C-Class', 'Start-Class', 'Stub-Class', 'FL-Class', 'List-Class', 'Unassessed-Class', 'NA-Class'],
        imagesRange: [0, this.maxNumberOfImages],
        categories: allCategories.map(a => a.value),
      }
  }
  
  changeIncludedImportances = value => {
    this.setState({
      includedImportances: value
    })
  }

  changeIncludedQualities = value => {
    this.setState({
      includedQualities: value
    })
  }

  changeImagesRange = value => {
    this.setState({
      imagesRange: value
    })
  }

  changeCategories = value => {
    if (value.length <= 0){
      this.setState({
        categories: allCategories.map(a => a.value)
      })
    }else{
      this.setState({
        categories: value
      })
    }
    
  }


  render (){

    const {includedImportances} = this.state;
    const {includedQualities} = this.state;
    const {categories} = this.state;
    const {imagesRange} = this.state;
    const filteredData = data.articles.filter(article=>includedImportances.indexOf(article.importance)!==-1)
                                      .filter(article=>includedQualities.indexOf(article.quality)!==-1)
                                      .filter(article=>categories.some(r=> article.categories.includes(r)))
                                      .filter(function(article){
                                        var max = Math.max(...imagesRange);
                                        var min = Math.min(...imagesRange);
                                        return article.n_images >= min && article.n_images <= max
                                      })

    return (
    <div className="App">
      <Layout className='container'>

        <Sider theme='light' className='viz-container' width='300px'>
          <View2
             maxNumberOfImages = {this.maxNumberOfImages}
             allCategories = {allCategories}
             changeIncludedImportances={this.changeIncludedImportances}
             changeIncludedQualities={this.changeIncludedQualities}
             changeImagesRange={this.changeImagesRange}
             changeCategories={this.changeCategories}
          />
          
        </Sider>
        <Layout>
          <Layout>
            <Sider theme='light' className='viz-container' width='35%'>Resume</Sider>
            <Content className='viz-container'>Resume viz</Content>
          </Layout>
          <Content className='viz-container' style={{height: '40%'}}>
            <View1 data={filteredData}></View1>
          </Content>
        </Layout>
        
      </Layout>
    </div>
    )
  };
}
