import React, { Component } from 'react';
import './view1.css';
import TestChart from '../../charts/test';

export default class View1 extends Component {
    render() {
        const {data} = this.props;
        const width = 260;
        const height = 260;
        return (
            <div id='view1' className='pane'>
                <TestChart data={data} width={width} height={height} />
            </div>
        )
    }
}